.popup__container {
  width: 430px;
  background-color: white;
  border-radius: 10px;
  position: relative;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
}

@media screen and (max-width: 1023px) {
  .popup__container {
    width: 400px;
  }
}

@media screen and (max-width: 767px) {
  .popup__container {
    width: 288px;
  }
}